*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: inherit;
  line-height: inherit;
  font-weight: 700;
}

p,
address {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input,
textarea {
  outline: none;
  font-family: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
}

/* roboto-regular - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v30-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-ext-regular.woff") format("woff");
}
/* roboto-500 - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v30-latin_cyrillic-ext-500.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-ext-500.woff") format("woff");
}
/* roboto-700 - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v30-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-v30-latin_cyrillic-ext-700.woff") format("woff");
}
:root {
  scroll-behavior: smooth;
}

::selection {
  color: #353642;
  background: #3d96e5;
}

::placeholder {
  color: #7c7c7c;
  font-family: inherit;
  font-size: 1.125rem;
}

.password-check-page__illustration, .hero__illustration, .qr-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

input {
  white-space: nowrap;
  text-overflow: ellipsis;
}

body {
  font-family: "Roboto", sans-serif;
  color: #353642;
  font-size: 1.125rem;
  line-height: 1.1;
  font-weight: 400;
}

.body-wrapper {
  background-image: linear-gradient(180deg, rgba(0, 117, 255, 0.38) 3.74%, rgba(0, 194, 255, 0.38) 21.16%, rgba(255, 255, 255, 0.38) 44.17%);
  background-repeat: no-repeat;
  min-height: 100%;
  padding: 4rem 4rem 0;
  overflow-x: hidden;
}
@media (max-width: 767.98px) {
  .body-wrapper {
    padding: 2rem;
  }
}
@media (max-width: 389.98px) {
  .body-wrapper {
    padding: 2rem 0;
  }
}

.container {
  max-width: calc(87.5rem + 2rem);
  padding: 0 1rem;
  margin: 0 auto;
  width: 100%;
}

.wrapper {
  margin: 0 auto;
  max-width: calc(87.5rem + 2rem);
  min-height: 12.5rem;
  background-color: #fff;
  box-shadow: 0 0.25rem 3.75rem rgba(0, 0, 0, 0.25);
  border-radius: 3.125rem;
  padding: 2rem 3rem;
}
@media (max-width: 767.98px) {
