.form__label--checkbox {
  position: relative;
  display: inline-block;
  font-size: 1rem;
  margin-top: 0.5rem;
  padding-left: 2.5rem;
  user-select: none;
}
.form__label--checkbox::before {
  content: "";
  position: absolute;
  width: 1rem;
  height: 1rem;
  margin-left: 1.25rem;
  border-radius: 0.25rem;
  border: 1px solid #3d96e5;
  top: 0;
  left: 0;
}
.form__label--checkbox::after {
  content: "";
  position: absolute;
  height: 0.5rem;
  width: 0.25rem;
  margin-left: 1.25rem;
  background-color: #fff;
  top: 0.5625rem;
  left: 0.25rem;
  transform-origin: left top;
  border-right: 2px solid #353642;
  border-top: 2px solid #353642;
