}
.form__input--checkbox {
  position: absolute;
  top: 0;
  left: 0;
  visibility: visible;
  opacity: 0;
  z-index: -28;
}
.form__input--checkbox:checked ~ .form__label--checkbox::after {
  opacity: 1;
  background-color: #3d96e5;
  border-right: 2px solid #fff;
  border-top: 2px solid #fff;
}
.form__input--checkbox:checked ~ .form__label--checkbox::before {
