.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: rgba(25, 25, 25, 0.3);
  transition: all 0.2s ease;
  visibility: hidden;
  opacity: 0;
  z-index: -100;
}
.modal.modal--open {
  visibility: visible;
  opacity: 1;
  z-index: 100;
  transition: all 0.3s ease;
}
.modal.modal--open .modal__body {
  animation: animation-down 0.3s;
}
.modal__body {
  position: relative;
  max-height: 100%;
  padding: 1.5rem 1.5rem 2rem;
  background-color: #fff;
  box-shadow: 0 0.25rem 3.75rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  transition: visibility 0.2s ease;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 101;
  animation: animation-up 0.3s;
}
@media (max-width: 575.98px) {
  .modal__body {
    padding: 2rem 1rem;
  }
}
.modal__head {
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5rem;
  background-color: #fff;
}
.modal__close-button {
  position: relative;
  display: inline-block;
  margin-left: auto;
  border: none;
  background-color: transparent;
  width: 1.5625rem;
  height: 1.25rem;
  z-index: 102;
  cursor: pointer;
}
.modal__close-button::before {
  content: "";
  position: absolute;
  height: 0.125rem;
  background-color: #3d96e5;
  width: 100%;
  top: 50%;
  left: 0;
  transform: rotate(45deg);
  z-index: 102;
  transition: background-color 0.2s ease;
}
.modal__close-button::after {
  content: "";
  position: absolute;
  height: 0.125rem;
  background-color: #3d96e5;
  width: 100%;
  top: 50%;
  left: 0;
  transform: rotate(-45deg);
  z-index: 102;
  transition: background-color 0.2s ease;
}
@media (hover: hover) {
  .modal__close-button:hover::before, .modal__close-button:hover::after {
    background-color: #1970bc;
  }
}
@keyframes animation-down {
  from {
    visibility: hidden;
    transform: translateY(-50%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes animation-up {
  from {
    transform: translateY(0);
  }
  to {
    visibility: hidden;
    transform: translateY(-50%);
  }
}
